import React from "react";
let level =
  prompt(
    "Please select level:\nType 1 for Easy\nType 2 for Medium\nType 3 for Hard",
    1
  ) || 1;
function generateValues(arrRec) {
  for (let index = 0; index < arrRec.length; index++) {
    for (let i = 0; i < 9; i++) {
      for (let j = 0; j < 9; j++) {
        if (arrRec[index].x === i) {
          document.querySelectorAll(`[data-x=\'${i}\']`).forEach((elem) => {
            if (parseInt(elem.getAttribute("data-y")) === arrRec[index].y) {
              elem.innerHTML = arrRec[index].value;
            }
          });
        }
      }
    }
  }
  document.querySelector(".loader").classList.remove("loader");
}
function renderGrid() {
  fetch(
    `https://cors-anywhere.herokuapp.com/http://www.cs.utep.edu/cheon/ws/sudoku/new/?size=9&level=${level}`,
    { headers: { "Access-Control-Allow-Origin": "*" } }
  )
    .then((response) => response.json())
    .then((final) => generateValues(final.squares));
  let arr = [];
  let count;
  let obj;
  let sectionHelper = [0, 0, 0, 1, 1, 1, 2, 2, 2];
  for (let i = 0; i < 9; i++) {
    for (let j = 0; j < 9; j++) {
      obj = {
        x: j,
        y: i,
        value: "",
      };
      if (i < 3) {
        count = 0;
        obj.section = count + sectionHelper[j];
      } else if (i > 2 && i < 6) {
        count = 3;
        obj.section = count + sectionHelper[j];
      } else {
        count = 6;
        obj.section = count + sectionHelper[j];
      }
      arr.push(obj);
    }
  }

  return arr;
}
function handleClick(e) {
  if (e.target.innerHTML === "") Game(e.target);
  checkWin();
}

let current;
let digArr = [];

function checkWin() {
  digArr = [0, 0, 0, 0, 0, 0, 0, 0, 0];
  document
    .querySelectorAll(".element")
    .forEach((elem) =>
      elem.innerHTML !== "" ? digArr[parseInt(elem.innerHTML) - 1]++ : ""
    );
  let flag = 0;
  let curr = document.querySelector(".selected");
  console.log(curr);
  digArr.forEach((dig, index) => {
    if (dig === 9) {
      flag++;
      if (index === curr.innerHTML - 1) curr.classList.add("digit_complete");
    }
  });
  if (flag === 9)
    setTimeout(() => {
      alert("Congratulations! You actually managed to solve it. Huge PP");
    }, 500);
  console.log(digArr);
}
console.log(digArr);
function currentDigit(e) {
  checkWin();
  console.log(digArr);
  if (e !== undefined) {
    if (current !== e.target.innerHTML) {
      document
        .querySelectorAll(`.digit`)
        .forEach((node) => node.classList.remove("selected"));
    }
    current = e.target.innerHTML;
    e.target.classList.add("selected");
  } else if (current === undefined) {
    alert("Please select digit");
    return "";
  }
  return current;
}
function Game(e) {
  let possible = true;
  let x = e.getAttribute("data-x");
  let y = e.getAttribute("data-y");
  let z = e.getAttribute("data-section");
  let horizontal = [];
  document
    .querySelectorAll(`[data-y=\'${y}\']`)
    .forEach((node) => horizontal.push(node.innerHTML));

  let vertical = [];
  document
    .querySelectorAll(`[data-x=\'${x}\']`)
    .forEach((node) => vertical.push(node.innerHTML));
  let home = [];
  document
    .querySelectorAll(`[data-section=\'${z}\']`)
    .forEach((node) => home.push(node.innerHTML));
  for (let index = 0; index < 9; index++) {
    if (
      current === horizontal[index] ||
      current === vertical[index] ||
      current === home[index]
    ) {
      possible = false;
      break;
    }
  }
  if (possible) e.innerHTML = currentDigit();
  else alert(`I don't think you can place a ${current} there. Look carefully.`);
}
function Grid() {
  let digits = [1, 2, 3, 4, 5, 6, 7, 8, 9];
  let arr = renderGrid();
  return (
    <div>
      <div className="container">
        {arr.map((element, index) => {
          return (
            <div
              key={index}
              className={
                element.x === 4 && element.y === 4
                  ? "loader element"
                  : "element"
              }
              data-x={element.x}
              data-y={element.y}
              data-section={element.section}
              onClick={handleClick}
            >
              {element.value}
            </div>
          );
        })}
      </div>
      <h3 className="legend">Select Input Digit</h3>
      <div className="digits">
        {digits.map((digit) => (
          <button className="digit" key={digit} onClick={currentDigit}>
            {digit}
          </button>
        ))}
      </div>
    </div>
  );
}
export default Grid;
